const MovingObject = require("./moving_object");
const Bullet = require("./bullet");
const Shield = require("./shield");
const Bomb = require("./bomb");
const Util = require("./util");
const FastEnemy = require("./fast_enemy");
const SlowEnemy = require("./slow_enemy");

const DEFAULTS = {
  COLOR: "red",
  SPEED: [0, 0],
  HEIGHT: 10,
  WIDTH: 10,
  HEALTH: 100,
};

class Avatar extends MovingObject {
  constructor(options) {
    options.pos = options.pos;
    options.height = DEFAULTS.HEIGHT,
    options.width = DEFAULTS.WIDTH,
    options.vel = DEFAULTS.SPEED;
    options.color = DEFAULTS.COLOR;
    super(options);
    this.flashActive;
    this.rectangle = true;
    this.ammo = 7;
    this.lives = 3;
    this.shieldCount = 3;
    this.bombCount = 3
    this.sound = options.sound;
    this.shieldActive = false;
    this.blasterFireActive = false;
    this.dead = false;
  }

  fireBullet() {
    if (this.dead) {
      console.log(this);
      this.checkForRespawn();
      return;
    }

    if (this.vel[0] === 0 && this.vel[1] === 0) {
      this.flash("Move!");
      return;
    }

    if (this.blasterFireActive) {
      return;
    }

    if (this.ammo <= 0) {
      this.flash("Reload");
      this.sound.playSound("emptyClip");
      return;
    }

    const relVel = Util.scale(
      Util.dir(this.vel),
      Bullet.SPEED
    );

    const bulletVel = [
      relVel[0] + this.vel[0], relVel[1] + this.vel[1]
    ];

    const bullet = new Bullet({
      pos: this.pos,
      vel: bulletVel,
      color: "lightgreen",
      game: this.game
    });

    this.game.generate(bullet);
    this.sound.playSound("blasterFired");
    this.blasterFireActive = true;
    this.ammo -= 1;
    setTimeout(() => { this.blasterFireActive = false; }, 100);
  }

  respawn() {
    if (this.dead === true) {
      this.pos = this.game.randomPosition();
      this.color = "red";
      this.dead = false;
    }
  }

  killed () {
    this.dead = true;
    this.color = "transparent";
  }

  deployShield() {
    if (this.shieldCount > 0 && !this.shieldActive) {
      const shield =  new Shield ({
        pos: [(this.pos[0] - this.vel[0] + (this.width/2)),
        (this.pos[1] - this.vel[1] + (this.height/2))],
        vel: this.vel,
        game: this.game,
        sound: this.sound
      });

      this.shieldCount -= 1;
      this.shieldActive = true;
      this.sound.playSound("shield");
      this.game.generate(shield);
    }
  }

  deployBomb() {
    if (this.bombCount > 0) {
      const bomb =  new Bomb ({
        pos: [(this.pos[0] - this.vel[0] + (this.width/2)),
        (this.pos[1] - this.vel[1] + (this.height/2))],
        vel: [0, 0],
        game: this.game,
        sound: this.sound,
        color: "#ffaa00",
        bigBomb: true,
        radius: 1
      });

      this.bombCount -= 1;
      this.game.generate(bomb);
    }
  }

  deployLittleBomb(newPos) {
    const bomb =  new Bomb ({
      pos: newPos,
      vel: [0, 0],
      game: this.game,
      sound: this.sound,
      color: "#91ffa9",
      littleBomb: true,
      radius: 1
    });

    this.game.generate(bomb);
  }

  deathExplosion() {
    const bomb =  new Bomb ({
      pos: [(this.pos[0] - this.vel[0] + (this.width/2)),
      (this.pos[1] - this.vel[1] + (this.height/2))],
      vel: [0, 0],
      game: this.game,
      sound: this.sound,
      color: "red",
      deathBomb: true,
      radius: 40
    });

    this.game.generate(bomb);
  }

  power(impulse) {
     this.vel[0] += impulse[0];
     this.vel[1] += impulse[1];

   }

   relocate() {
     this.sound.playSound("teleport");
     let newPos = this.game.randomPosition();
     this.deployLittleBomb(newPos);
     setTimeout(() => {
       this.pos = newPos;
     }, 100);
     this.vel = [0, 0];
   }

  flash(string) {
    let display = document.getElementById("flash");

    if (this.vel[0] === 0 && this.vel[1] === 0 && !this.flashActive) {
      this.flashActive = true;
      setTimeout(() => this.displayFlash(display, string), 0);
      setTimeout(() => this.displayFlash(display, ""), 650);
      this.flashActive = false;
    }

    if (this.ammo === 0 && !this.flashActive) {
      this.flashActive = setInterval(() =>
      this.displayFlash(display, string), 400);
    }
  }

  displayFlash(display, string) {
    if (display.innerHTML) {
      display.innerHTML = "";
    } else {
      display.innerHTML = string;
    }
  }

  collideWith(object) {
    if (!this.dead) {
      if (object.constructor.name === "SlowEnemy") {
        object.damage();
        this.deathExplosion();
        this.killed();
        this.sound.playSound("shutdown");
        this.sound.playDeathMusic();
        return true;
      } else if (object instanceof FastEnemy) {
        object.remove();
        this.deathExplosion();
        this.killed();
        this.sound.playSound("shutdown");
        this.sound.playDeathMusic();
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }
}

module.exports = Avatar;



